

import { Direction } from '@angular/cdk/bidi';
import { InjectionToken, QueryList } from '@angular/core';
import { TDSCarouselContentDirective } from './carousel-content.directive';
import { TDSCarouselBaseStrategy } from './strategies/base-strategy';

export type TDSCarouselEffects = 'fade' | 'scrollx' | string;
export type TDSCarouselDotPosition = 'top' | 'bottom' | 'left' | 'right' | string;

export interface TDSCarouselComponentAsSource {
  carouselContents: QueryList<TDSCarouselContentDirective>;
  el: HTMLElement;
  tdsTransitionSpeed: number;
  vertical: boolean;
  slickListEl: HTMLElement;
  slickTrackEl: HTMLElement;
  activeIndex: number;
  dir: Direction;
}

export interface TDSCarouselStrategyRegistryItem {
  name: string;
  strategy: TDSCarouselBaseStrategy;
}

export const TDS_CAROUSEL_CUSTOM_STRATEGIES = new InjectionToken<TDSCarouselStrategyRegistryItem[]>('tds-carousel-custom-strategies');

export interface PointerVector {
  x: number;
  y: number;
}

export interface FromToInterface {
  from: number;
  to: number;
}
