

import { Direction, Directionality } from '@angular/cdk/bidi';
import { BooleanInput, NumberInput } from '@angular/cdk/coercion';
import { LEFT_ARROW, RIGHT_ARROW } from '@angular/cdk/keycodes';
import { Platform } from '@angular/cdk/platform';
import {
  AfterContentInit,
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChildren,
  ElementRef,
  EventEmitter,
  Inject,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  QueryList,
  Renderer2,
  SimpleChanges,
  TemplateRef,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { InputBoolean, InputNumber, TDSSafeAny } from 'tds-ui/shared/utility';
import { TDSConfigKey, TDSConfigService, WithConfig } from 'tds-ui/core/config';
import { TDSDragService } from 'tds-ui/core/services';

import { TDSCarouselContentDirective } from './carousel-content.directive';
import { TDSCarouselBaseStrategy } from './strategies/base-strategy';
import { TDSCarouselOpacityStrategy } from './strategies/opacity-strategy';
import { TDSCarouselTransformStrategy } from './strategies/transform-strategy';
import {
  FromToInterface,
  TDSCarouselDotPosition,
  TDSCarouselEffects,
  TDSCarouselStrategyRegistryItem,
  TDS_CAROUSEL_CUSTOM_STRATEGIES,
  PointerVector
} from './typings';
import { TDSResizeService } from 'tds-ui/core/services';

const TDS_CONFIG_MODULE_NAME: TDSConfigKey = 'carousel';

@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  selector: 'tds-carousel',
  exportAs: 'tdsCarousel',
  preserveWhitespaces: false,
  template: `
    <div class="slick-initialized slick-slider" [class.slick-vertical]="tdsDotPosition === 'left' || tdsDotPosition === 'right'">
      <div
        #slickList
        class="slick-list"
        tabindex="-1"
        (keydown)="onKeyDown($event)"
        (mousedown)="pointerDown($event)"
        (touchstart)="pointerDown($event)"
      >
        <!-- Render carousel items. -->
        <div class="slick-track" #slickTrack>
          <ng-content></ng-content>
        </div>
      </div>
      <!-- Render dots. -->
      <ul
        class="slick-dots"
        *ngIf="tdsDots"
        [class.slick-dots-top]="tdsDotPosition === 'top'"
        [class.slick-dots-bottom]="tdsDotPosition === 'bottom'"
        [class.slick-dots-left]="tdsDotPosition === 'left'"
        [class.slick-dots-right]="tdsDotPosition === 'right'"
      >
        <li *ngFor="let content of carouselContents; let i = index" [class.slick-active]="content.isActive" (click)="onLiClick(i)">
          <ng-template [ngTemplateOutlet]="tdsDotRender || renderDotTemplate" [ngTemplateOutletContext]="{ $implicit: i }"></ng-template>
        </li>
      </ul>
    </div>

    <ng-template #renderDotTemplate let-index>
      <button>{{ index + 1 }}</button>
    </ng-template>
  `,
  host: {
    '[class.tds-carousel-vertical]': 'vertical',
    '[class.tds-carousel-rtl]': `dir ==='rtl'`,
    'class':'block relative overflow-hidden w-full h-full'
  }
})
export class TDSCarouselComponent implements AfterContentInit, AfterViewInit, OnDestroy, OnChanges, OnInit {
  readonly _tdsModuleName: TDSConfigKey = TDS_CONFIG_MODULE_NAME;
  static ngAcceptInputType_tdsEnableSwipe: BooleanInput;
  static ngAcceptInputType_tdsDots: BooleanInput;
  static ngAcceptInputType_tdsAutoPlay: BooleanInput;
  static ngAcceptInputType_tdsAutoPlaySpeed: NumberInput;
  static ngAcceptInputType_tdsTransitionSpeed: NumberInput;

  @ContentChildren(TDSCarouselContentDirective) carouselContents!: QueryList<TDSCarouselContentDirective>;

  @ViewChild('slickList', { static: false }) slickList?: ElementRef;
  @ViewChild('slickTrack', { static: false }) slickTrack?: ElementRef;

  @Input() tdsDotRender?: TemplateRef<{ $implicit: number }>;
  @Input() @WithConfig() tdsEffect: TDSCarouselEffects = 'scrollx';
  @Input() @WithConfig() @InputBoolean() tdsEnableSwipe: boolean = true;
  @Input() @WithConfig() @InputBoolean() tdsDots: boolean = true;
  @Input() @WithConfig() @InputBoolean() tdsAutoPlay: boolean = false;
  @Input() @WithConfig() @InputNumber() tdsAutoPlaySpeed: number = 3000;
  @Input() @InputNumber() tdsTransitionSpeed = 500;

  /**
   * this property is passed directly to an TDSCarouselBaseStrategy
   */
  @Input() tdsStrategyOptions: TDSSafeAny = undefined;

  @Input()
  // @ts-ignore
  @WithConfig()
  set tdsDotPosition(value: TDSCarouselDotPosition) {
    this._dotPosition = value;
    if (value === 'left' || value === 'right') {
      this.vertical = true;
    } else {
      this.vertical = false;
    }
  }

  get tdsDotPosition(): TDSCarouselDotPosition {
    return this._dotPosition;
  }

  private _dotPosition: TDSCarouselDotPosition = 'bottom';

  @Output() readonly tdsBeforeChange = new EventEmitter<FromToInterface>();
  @Output() readonly tdsAfterChange = new EventEmitter<number>();

  activeIndex = 0;
  el: HTMLElement;
  slickListEl!: HTMLElement;
  slickTrackEl!: HTMLElement;
  strategy?: TDSCarouselBaseStrategy;
  vertical = false;
  transitionInProgress: TDSSafeAny | null = null;
  dir: Direction = 'ltr';

  private destroy$ = new Subject<void>();
  private gestureRect: ClientRect | null = null;
  private pointerDelta: PointerVector | null = null;
  private isTransiting = false;
  private isDragging = false;

  constructor(
    elementRef: ElementRef,
    public readonly tdsConfigService: TDSConfigService,
    private readonly renderer: Renderer2,
    private readonly cdr: ChangeDetectorRef,
    private readonly platform: Platform,
    private readonly resizeService: TDSResizeService,
    private readonly tdsDragService: TDSDragService,
    @Optional() private directionality: Directionality,
    @Optional() @Inject(TDS_CAROUSEL_CUSTOM_STRATEGIES) private customStrategies: TDSCarouselStrategyRegistryItem[]
  ) {
    this.tdsDotPosition = 'bottom';

    this.renderer.addClass(elementRef.nativeElement, 'tds-carousel');
    this.el = elementRef.nativeElement;
  }
  ngOnInit(): void {
    this.dir = this.directionality.value;

    this.directionality.change?.pipe(takeUntil(this.destroy$)).subscribe((direction: Direction) => {
      this.dir = direction;
      this.markContentActive(this.activeIndex);
      this.cdr.detectChanges();
    });
  }

  ngAfterContentInit(): void {
    this.markContentActive(0);
  }

  ngAfterViewInit(): void {
    this.slickListEl = this.slickList!.nativeElement;
    this.slickTrackEl = this.slickTrack!.nativeElement;

    this.carouselContents.changes.pipe(takeUntil(this.destroy$)).subscribe(() => {
      this.markContentActive(0);
      this.layout();
    });

    this.resizeService
      .subscribe()
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {
        this.layout();
      });

    this.switchStrategy();
    this.markContentActive(0);
    this.layout();

    // If embedded in an entry component, it may do initial render at an inappropriate time.
    // ngZone.onStable won't do this trick
    // TODO: need to change this.
    Promise.resolve().then(() => {
      this.layout();
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    const { tdsEffect, tdsDotPosition } = changes;

    if (tdsEffect && !tdsEffect.isFirstChange()) {
      this.switchStrategy();
      this.markContentActive(0);
      this.layout();
    }

    if (tdsDotPosition && !tdsDotPosition.isFirstChange()) {
      this.switchStrategy();
      this.markContentActive(0);
      this.layout();
    }

    if (!this.tdsAutoPlay || !this.tdsAutoPlaySpeed) {
      this.clearScheduledTransition();
    } else {
      this.scheduleNextTransition();
    }
  }

  ngOnDestroy(): void {
    this.clearScheduledTransition();
    if (this.strategy) {
      this.strategy.dispose();
    }

    this.destroy$.next();
    this.destroy$.complete();
  }

  onKeyDown(e: KeyboardEvent): void {
    if (e.keyCode === LEFT_ARROW) {
      e.preventDefault();
      this.pre();
    } else if (e.keyCode === RIGHT_ARROW) {
      this.next();
      e.preventDefault();
    }
  }

  onLiClick = (index: number) => {
    if (this.dir === 'rtl') {
      this.goTo(this.carouselContents.length - 1 - index);
    } else {
      this.goTo(index);
    }
  };
  next(): void {
    this.goTo(this.activeIndex + 1);
  }

  pre(): void {
    this.goTo(this.activeIndex - 1);
  }

  goTo(index: number): void {
    if (this.carouselContents && this.carouselContents.length && !this.isTransiting) {
      const length = this.carouselContents.length;
      const from = this.activeIndex;
      const to = (index + length) % length;
      this.isTransiting = true;
      this.tdsBeforeChange.emit({ from, to });
      this.strategy!.switch(this.activeIndex, index).subscribe(() => {
        this.scheduleNextTransition();
        this.tdsAfterChange.emit(index);
        this.isTransiting = false;
      });
      this.markContentActive(to);
      this.cdr.markForCheck();
    }
  }

  private switchStrategy(): void {
    if (this.strategy) {
      this.strategy.dispose();
    }

    // Load custom strategies first.
    const customStrategy = this.customStrategies ? this.customStrategies.find(s => s.name === this.tdsEffect) : null;
    if (customStrategy) {
      this.strategy = new (customStrategy.strategy as TDSSafeAny)(this, this.cdr, this.renderer, this.platform);
      return;
    }

    this.strategy =
      this.tdsEffect === 'scrollx'
        ? new TDSCarouselTransformStrategy(this, this.cdr, this.renderer, this.platform)
        : new TDSCarouselOpacityStrategy(this, this.cdr, this.renderer, this.platform);
  }

  private scheduleNextTransition(): void {
    this.clearScheduledTransition();
    if (this.tdsAutoPlay && this.tdsAutoPlaySpeed > 0 && this.platform.isBrowser) {
      this.transitionInProgress = setTimeout(() => {
        this.goTo(this.activeIndex + 1);
      }, this.tdsAutoPlaySpeed);
    }
  }

  private clearScheduledTransition(): void {
    if (this.transitionInProgress) {
      clearTimeout(this.transitionInProgress);
      this.transitionInProgress = null;
    }
  }

  private markContentActive(index: number): void {
    this.activeIndex = index;

    if (this.carouselContents) {
      this.carouselContents.forEach((slide, i) => {
        if (this.dir === 'rtl') {
          slide.isActive = index === this.carouselContents.length - 1 - i;
        } else {
          slide.isActive = index === i;
        }
      });
    }

    this.cdr.markForCheck();
  }

  /**
   * Drag carousel.
   */
  pointerDown = (event: TouchEvent | MouseEvent) => {
    if (!this.isDragging && !this.isTransiting && this.tdsEnableSwipe) {
      this.clearScheduledTransition();
      this.gestureRect = this.slickListEl.getBoundingClientRect();

      this.tdsDragService.requestDraggingSequence(event).subscribe(
        delta => {
          this.pointerDelta = delta;
          this.isDragging = true;
          this.strategy?.dragging(this.pointerDelta);
        },
        () => {},
        () => {
          if (this.tdsEnableSwipe && this.isDragging) {
            const xDelta = this.pointerDelta ? this.pointerDelta.x : 0;

            // Switch to another slide if delta is bigger than third of the width.
            if (Math.abs(xDelta) > this.gestureRect!.width / 3) {
              this.goTo(xDelta > 0 ? this.activeIndex - 1 : this.activeIndex + 1);
            } else {
              this.goTo(this.activeIndex);
            }

            this.gestureRect = null;
            this.pointerDelta = null;
          }

          this.isDragging = false;
        }
      );
    }
  };

  layout(): void {
    if (this.strategy) {
      this.strategy.withCarouselContents(this.carouselContents);
    }
  }
}
